
:root {
  --color-morado: #614ea0;
  --color-naranja: #ff9c63;
}


.login {
    font-family: "Poppins", sans-serif;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    border-color: 2px solid var(--color-naranja);
    color: white;    
    padding: 0 15px;
  }
  .login__form {
    background-color: var(--color-morado);
    max-width: 380px;
    border-radius: 20px 0 0 20px;
    border-color: var(--color-naranja);
    padding: 20px 5px 0 10px;
;
  }
  .login h2 {
    text-align: center;
  }
  .login .formulario {
    border-color: var(--color-naranja);
  }
  .login .formulario label {
    padding: 10px 0px 10px 10px;
    display: block;
  }
  .login .formulario__input {
    transition: width 0.15s ease-in-out;
    margin: 5px auto;
    display: block;
    width: 95%;
    padding: 10px 20px 10px 40px;
    outline: none;
    border: none;
    border-radius: 4px;
    background-repeat: no-repeat;
    background-position-y: center;
    background-position-x: 10px;
    font-size: 1.1rem;
  }
  .login .formulario__input:focus {
    width: 100%;
    box-shadow: 0px 0px 8px white;
  }
  .login .formulario__input[type="email"] {
    /* background-image: url("./assets/img/email.svg"); */
  }
  .login .formulario__input[type="email"]:focus {
    /* background-image: url("./assets/img/emailhover.svg"); */
  }
  .login .formulario__input[type="password"] {
    /* background-image: url("./assets/img/password.svg"); */
  }
  .login .formulario__input[type="password"]:focus {
    /* background-image: url("./assets/img/passwordhover.svg"); */
  }
  .login .formulario__submit {
    width: 95%;
    display: block;
    margin: 20px auto;
    background-color: #ff9c63;
    outline: none;
    padding: 10px 0;
    border-radius: 5px;
    color: white;
    border: none;
    text-transform: uppercase;
    font-weight: bold;
    cursor: pointer;
  }
  .login .formulario__submit:hover {
    background-color: #ff7c30;
  }
  
  .login__contrasena{
    text-align: center;
  }

  .login__contrasena:hover{
    cursor: pointer;
    text-decoration-line: underline;
  }