@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --color-morado: #614ea0;
  --color-naranja: #ff9c63;
  --on: rgba(133, 255, 122, 1);
  --off: rgba(44, 63, 82, 1);
  --selected: rgba(21, 32, 42, 1);
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}


/* ZONA  PAGINA DE INICIO */

.portada_cuerpo {
