/* ZONA DEL BUSCADOR */

.buscador{
  width: 250px !important;
}

.switch__menu {
  width: 130px;
  height: 40px;
  position: absolute;
  left: calc(50% - 155px);
  top: calc(50% - 45px);
  user-select: none;
}

.switch__menu:first-of-type {
  margin-top: 25px; /* Switch de arriba general */
}

.switch__menu:first-of-type .inner {
  transition: 0.4s ease-in-out;
  transition-delay: 0s;
}

.switch__menu:first-of-type .outer {
  transition: transform 0.4s ease-in-out;
  transition-delay: 0.4s;
}

.switch__menu:first-of-type .outer::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  border-radius: 80px;
  background: linear-gradient(to right, var(--color-morado), #ec38bc);
}

.switch__menu:first-of-type input:checked ~ .outer {
  animation: shift 0.8s ease-in-out 1 forwards;
  transform: rotate(180deg);
}

.switch__menu:first-of-type input:checked ~ .outer:before {
  background: linear-gradient(to left, var(--color-naranja), #ec38bc);
}

@keyframes shift {
  0% {
    width: 120px;
    transform: rotate(0deg);
  }
  25% {
    width: 60px;
    transform: rotate(0deg);
  }
  50% {
    width: 80px;
    transform: rotate(180deg);
  }
  100% {
    width: 130px; /* switch 1 contorno */
    transform: translateX(80px) rotate(180deg); /* switch bolita 1 */
  }
}

.switch__menu:first-of-type input:checked ~ .outer ~ .inner {
  transition-delay: 0.4s;
  transform: translateX(65px); /* switch 1ra bolita */
  box-shadow: inset 5px -10px 15px 0 rgba(0, 0, 0, 0.5);
}

.switch__menu:nth-of-type(2) {
  margin-top: 25px; /* siwtch 2: ubicacion general */
  margin-left: 80px;
}

.swith__textcarta,
.swith__textmapa {
  z-index: 1;
  position: absolute;
  left: 47px; /* Moviendo la ubicación del texto */
  top: 5px;
  color: white;
}
.switch__menu:nth-of-type(2) input:checked {
  z-index: 7;
}

.switch__menu:nth-of-type(2) input:checked ~ .outer {
  transform: rotate(180deg);
  margin-left: 90px; /*  switch contorno 2*/
  background: linear-gradient(to left, var(--color-naranja), #ec38bc);
}

.switch__menu:nth-of-type(2) input:checked ~ .inner {
  margin-left: 73px; /* Switch 2 bolita - direcion derecha */
  box-shadow: inset 5px -10px 15px 0 rgba(0, 0, 0, 0.5);
}

.outer,
.switch__input {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 9;
  border-radius: 80px;
  opacity: 0;
}

.outer:nth-of-type(2),
.switch__input:nth-of-type(2) {
  z-index: 8;
}

.outer {
  background: linear-gradient(to right, var(--color-morado), #ec38bc);
  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.25),
    0 0 20px 0 rgba(0, 0, 0, 0.15);
  opacity: 1;
  z-index: 0;
  transform-origin: 20px 20px;
  transition: transform 0.3s ease-in-out, margin 0.3s ease-in-out;
  transition-delay: 0s, 0.3s;
}

.inner {
  position: absolute;
  width: 30px;
  height: 30px;
  background: #fff;
  box-shadow: inset -5px -10px 15px 0 rgba(0, 0, 0, 0.5);
  background-position: -35px -30px;
  background-size: 120%;
  position: absolute;
  left: calc(50% - 50px);
  top: calc(50% - 15px);
  border-radius: 500%;
  transition: margin 0.3s ease-in-out, box-shadow 0.6s ease-in-out;
  transition-delay: 0.3s, 0s;
}



