/* ZONA DEL CARRITO DE COMPRAS: SELECCION DE CATALOGOS */

.zonaventa {
  background-color: var(--color-morado);
  width: 100%;
  display: flex;
  margin: 0 0 0 60px;
}
.zonaventa__tabla {
  background-color: white;
  display: flex;
  flex-direction: column;
  align-content: flex-start;
  border-radius: 20px;
  margin: 0 0 0 0;
  padding: 10px 0 0 10px;
}

.zonaventa__paneldecontrol {
  border-radius: 20px;
  padding: 20px 5px 0 5px;
  border-color: var(--color-naranja);
  margin: auto 0;
}

/* ZONA LISTADO DE SUB-CATEGORIAS */

.categorias_lista {
  list-style: none;
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
}

.categorias_lista li label {
  border: 2px solid var(--color-naranja);
  color: var(--color-morado);
  border-radius: 20px;
  user-select: none;
  transition: all 0.2s;
  cursor: pointer;
  padding: 4px 6px;
}

.categorias_lista li label::before {
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  font-size: 12px;
  padding: 2px 6px 2px 2px;
  content: "\f067";
  transition: transform 0.3s ease-in-out;
}

.categorias_lista li input[type="checkbox"]:checked + label::before {
  content: "\f00c";
  transform: rotate(-360deg);
  transition: transform 0.3s ease-in-out;
}

.categorias_lista li input[type="checkbox"]:checked + label {
  border: 2px solid var(--color-naranja);
  background-color: var(--color-morado);
  color: #fff;
  transition: all 0.2s;
}

.categorias_lista li input[type="checkbox"] {
  display: absolute;
  position: absolute;
  opacity: 0;
}

.categorias_lista li input[type="checkbox"]:focus + label {
  border: 2px solid #e9a1ff;
}

