/* Control de pagos */
.card__controlaltura {
  height: auto;
  overflow: hidden;
}

.card__controlPago {
  max-width: 650px;
  border-radius: 20px 0 0 20px;
  background-color: var(--color-morado);
  color: white;
  border: none;
  box-sizing: border-box;
}

.card__delivery{
  width: 60px;
  height: 20px;
  display: flex;
}

.card__delivery label{
  display: inline-block;
  cursor: pointer;
  position: relative;
}

.card__delivery img{
  height: 30px;
}

.card__delivery span{
  display: inline-block;
  background-color: transparent;
  width: 25px;
  height: 25px;
  transform-origin: center;
  border: 2px solid white;
  border-radius: 50%;
  vertical-align: -6px;
  margin-right: 10px;
  transition: background-color 150ms 200ms,
    transform 350ms cubic-bezier(0.78, -1.22, 0.17, 1.89);
}

.card__delivery span::before{
  content: "";
  width: 0;
  height: 2px;
  border-radius: 2px;
  background-color: white;
  position: absolute;
  transform: rotate(45deg);
  top:  10px;
  left: 6px;
  transition: width 50ms ease 50ms;
  transform-origin: 0% 0%;
}

.card__delivery span::after{
  content: "";
  width: 0;
  height: 2px;
  background-color: white;
  position: absolute;
  transform: rotate(305deg);
  top: 13px;
  left: 9px;
  transition: width 50ms ease;
  transform-origin: 0% 0%;
}
.card__delivery label:hover span::before{
  width: 6px;
  transition: width 100ms ease;
}

.card__delivery label:hover span::after{
  width: 10px;
  transition: width 100ms ease;
}

.card__delivery_input{
  display: none;
}

.card__delivery_labelspan{
  background-color: white !important;
  transform: scale(1.25) !important;
  color: #414b59;
}

.card__delivery_labelspan::after{
  width: 10px !important;
  background-color: var(--color-naranja) !important;
  transition: width 150ms ease 100ms !important;
}

.card__delivery_labelspan::before{
  width: 6px !important;
  background-color: var(--color-naranja) !important;
  transition: width 150ms ease 100ms !important;
}



